'use client'

import { createContext } from 'react';
import { useReducer, useContext } from 'react';

const SideNavContext = createContext();
const SideNavDispatchConText = createContext();

export const useSideNavContext = () => {
    return useContext(SideNavContext);
}

export const useSideNavDispatchContext = () => {
    return useContext(SideNavDispatchConText);
}

const reducer = (state, action) => {
    switch(action.type) {
        case 'SET_CLASS_TYPE':
            return {
                ...state,
                currentClass: action.classType
            }
        case 'TOGGLE_DROPDOWN':
            return {
                ...state,
                dropDownOpen: !state.dropDownOpen
            }
        case 'SET_ACTIVE_MENU':
            return {
                ...state,
                activeMenu: action.menu
            }
        
        default:
            return state;
    }
}

export default function SideNavContexts({children}) {

    const [state, dispatch] = useReducer(reducer, {
        currentClass: null,
        dropDownOpen: false,
        activeMenu: null,
    });

    return (
        <SideNavContext.Provider value={state}>
            <SideNavDispatchConText.Provider value={dispatch}>
                {children}
            </SideNavDispatchConText.Provider>
        </SideNavContext.Provider>
    );_
}